.section {
  padding: 60px 0;
  &:nth-child(2n) {
    background: #eeeeee;
  }
  &__title {
    font-size: 2.2rem;
    text-transform: uppercase;
    font-weight: 900;
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    &:after {
      content: "";
      position: absolute;
      width: 100px;
      height: 5px;
      border-radius: 25px;
      bottom: -20px;
      background: $primaryColor;
    }
  }
  &__content {
    padding-top: 60px;
  }
  &__row {
    display: flex;
    align-items: center;
    &.btn-row {
      padding-top: 60px;
    }
  }
  &__column {
    flex-basis: 100%;
    .btn {
      margin-top: 60px;
    }
  }
  &__column + &__column {
    margin-left: 60px;
  }
  &__img {
    max-width: 100%;
  }
}
@media all and(max-width:768px) {
  .section__row {
    align-items: center;
    flex-direction: column;
  }
  .section__column {
    & + & {
      margin-left: 0;
      margin-top: 30px;
    }
  }
  .welcome__text {
    font-size: 25px;
    width: 70%;
  }
}
@media all and(max-width:375px) {
  .welcome__text {
    font-size: 20px;
  }
}
