.welcome{
	position: relative;	
	height: calc(100vh - 52px);
	width: 100%;
	background: url(../img/welcome.jpg) top center no-repeat;
	background-attachment: fixed;
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	&::before{
		content: "";
		position: absolute;	
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.8);
		z-index: 0;	
	}
	&__text{
		margin-top: 60px;
		position: relative;	
		z-index: 3;
		font-size: 2em;
		width: 50%;	
		.text{
			text-align: center;
			color:white;
			line-height: 1.3;
		}
		.text + .text{
			margin-top: 15px;
		}
	}
}
.logo{
	position: relative;	
	z-index: 3;
	text-align: center;
}